@charset "UTF-8";

@font-face {
  font-family: "hustle";
  src:url("../fonts/hustle.eot");
  src:url("../fonts/hustle.eot?#iefix") format("embedded-opentype"),
    url("../fonts/hustle.woff") format("woff"),
    url("../fonts/hustle.ttf") format("truetype"),
    url("../fonts/hustle.svg#hustle") format("svg");
  font-weight: normal;
  font-style: normal;

}

.wphi-font:before {
	font-family: "hustle";
	content: attr(data-icon);
	font-style: normal;
	font-weight: normal;
	font-variant: normal;
	text-transform: none;
	speak: none;
	line-height: 1;
	-moz-osx-font-smoothing: grayscale;
	-webkit-font-smoothing: antialiased;
}

.wphi-check:before {
  content: "\64";
}
.wphi-heart:before {
  content: "\68";
}
.wphi-error:before {
  content: "\78";
}
.wphi-align-justify:before {
  content: "\61";
}
.wphi-plus:before {
  content: "\41";
}
.wphi-align-left:before {
  content: "\6c";
}
.wphi-align-right:before {
  content: "\6d";
}
.wphi-eye:before {
  content: "\75";
}
.wphi-address:before {
